.c4l-quote {
	box-sizing: border-box;

	&.is-style-compact-width .c4l-quote-content {
		width: 75%;
		margin-left: auto;
		margin-right: auto;
	}

	&.is-style-full-width .c4l-quote-content {
		width: 100%;
	}

	.c4l-quote-content {
		width: 75%;
		margin-left: auto;
		margin-right: auto;
		box-sizing: border-box;
		font-family: Lora, "Iowan Old Style", "Apple Garamond", Baskerville, "Times New Roman", "Droid Serif", Times, "Source Serif Pro", serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
	}

	.c4l-quote-text {
		font-style: italic;
		border-left: 4px solid #387af1;
		padding-left: 36px;
		padding-right: 36px;
		margin-right: 16px;
		margin-top: 4px;
		margin-bottom: 2px;
	}

	.c4l-quote-caption {
		font-size: 13px;
		margin-top: 12px;
		text-align: right;
	}

}